<template>
	<view class="box">
		<view class="content">

			<!-- 报名信息 -->
			<view class="registration-information">
				<view class="title">报名信息</view>
				<view class="information">
					<label for="">昵称</label>
					<input type="text" v-model="formData.NickName" @blur.once="handleNickName" placeholder="请输入昵称">
				</view>
				<view class="information">
					<label for="">性别</label>
					<view class="man-woman">
						<!-- :class="[manWomanIndex===index?'man':'','woman']" -->
						<view class="woman"
							:style="{background: manWomanIndex===index?'rgba(0, 208, 143, 0.05)':'#fff',border: manWomanIndex===index? '2rpx solid #00D08F':'2rpx solid #ccc'}"
							v-for="(item,index) in manWoman" :key="index" @click="handleIndex(index)">
							<image :src="item.img" mode=""></image>
							<text>{{item.text}}</text>
						</view>
						<!-- <view class="woman">
							<image src="@/static/img/icon_female@3x.png"></image>
							<text>女</text>
						</view> -->
					</view>
				</view>
				<view class="information">
					<label for="">手机号</label>
					<input type="text" v-model="formData.Phone" placeholder="请输入手机号">
				</view>
			</view>

			<!-- 添加挂件人 -->
			<view class="pendant-man">
				<view class="title">
					<view class="icons" @click="addPendantMan">
						<image src="@/static/img/icon_add@3x.png"></image>
						<!-- <uni-icons type="plusempty" color="#00D08F"></uni-icons> -->
					</view>
					<text>添加挂件人信息</text>
				</view>
				<view class="">
					<view class="pendant" v-for="(item,index) in hangerList" :key="index">
						<view class="">
							<text>{{item.name}}</text>
							<image :src="item.sex==='男'?man:woman"></image>
						</view>
						<view class="pendant-right">
							<view class="" @click="delectHanger(index)">
								<image src="@/static/img/icon_del@2x.png"></image>
								<text>删除</text>
							</view>
							<view class="" @click="editHanger(index)">
								<image src="@/static/img/icon_edit@2x.png"></image>
								<text>编辑</text>
							</view>
						</view>
					</view>
				</view>
			</view>

			<!-- 活动分组 -->
			<view class="select-grouping">
				<view class="title">选择活动分组</view>
				<view class="activity" v-for="(item,index) in signDetailList" :key="index"
					@click="handleGrouping(index)">
					<view class="top-title">
						<view class="top-title-left">
							<view class="radio" :style="{background: signDetail===index?'#00D08F':'#fff'}"></view>
							<text>{{item.GroupName}}</text>
						</view>
						<view class="money">
							<text>￥{{item.Price}}</text>
							<text>/人</text>
						</view>
					</view>
					<view class="activity-content">{{item.GroupDescribe}}</view>
					<view class="time-people">
						<!-- <view class="">11月26日 09:00 - 12:00</view> -->
						<view class="">已报名：{{item.GourpSign}}/{{item.SignNumber}}</view>
					</view>
				</view>
			</view>
		</view>

		<!-- 底部 -->
		<view class="bottom">
			<view class="collection">
				<view class="">合计</view>
				<view class="">￥{{moneyTitle}}</view>
			</view>
			<view class="btn" @click="eventRegistration">提交报名</view>
		</view>

		<!-- 弹框 -->
		<uni-popup ref="popup" type="bottom">
			<view class="popup">
				<view class="popup-top">
					<view class="title">添加挂人信息</view>
					<view class="add-information">
						<view class="information">
							<label for="">昵称</label>
							<input type="text" v-model="hangerObject.name" placeholder="请输入昵称">
						</view>
						<view class="information">
							<label for="">性别</label>
							<view class="man-woman">
								<view class="woman"
									:style="{background: sex===index?'rgba(0, 208, 143, 0.05)':'#fff',border: sex===index? '2rpx solid #00D08F':'2rpx solid #ccc'}"
									v-for="(item,index) in manWoman" :key="index" @click="handleSex(index)">
									<image :src="item.img" mode=""></image>
									<text>{{item.text}}</text>
								</view>
							</view>
						</view>
					</view>
					<!-- <view class="avtivity-title">选择活动分组</view> -->
				</view>
				<!-- <view class="popup-middle">
					<view class="activity" v-for="(item,index) in 6">
						<view class="top-title">
							<view class="top-title-left">
								<view class="radio"></view>
								<text>分组名称</text>
							</view>
							<view class="money">
								<text>￥49.00</text>
								<text>/人</text>
							</view>
						</view>
						<view class="activity-content">
							小组描述内容内容内容内容内容内容内容内容内容内容内容
						</view>
						<view class="time-people">
							<view class="">11月26日 09:00 - 12:00</view>
							<view class="">已报名：0/99</view>
						</view>
					</view>
				</view> -->
				<view class="popup-bottom">
					<view class="close" @click="$refs.popup.close()">取消</view>
					<view class="save" @click="onSubmit">确认</view>
				</view>
			</view>
		</uni-popup>
	</view>
</template>

<script>
	import {
		PostCreateActivitySignAjax,
		GetSignDetailAjax
	} from '@/apis/api/modules/activity'
	import {
		GetOrderPayAjax
	} from '@/apis/api/modules/buyland'
	import man from '@/static/img/icon_male@3x.png'
	import woman from '@/static/img/icon_female@3x.png'
	export default {
		data() {
			return {
				man,
				woman,
				addNoHanger: null, //是否添加挂件人
				editIndex: null, //编辑挂件人索引
				activityId: '',
				signDetailList: [],
				signDetail: null,
				hangerList: [],
				hangerObject: {
					name: '',
					sex: ''
				},
				peopleTotal: 0,
				moneyTitle: 0,
				price: 0,
				Pendant: [],
				formData: {
					GroupingID: "",
					Gender: "",
					Phone: "",
					NickName: "",
					Pendant: ""
				},
				manWomanIndex: null,
				sex: null,
				manWoman: [{
					img: require("@/static/img/icon_male@3x.png"),
					text: '男'
				}, {
					img: require("@/static/img/icon_female@3x.png"),
					text: '女'
				}]
			}
		},
		onLoad(e) {
			this.activityId = e.activityId
			this.getSignDetail()
		},
		methods: {
			/**
			 * 获取活动分组详情
			 */
			async getSignDetail() {
				const result = await GetSignDetailAjax({
					ActivityID: this.activityId
				})
				this.signDetailList = result.Result
			},
			/**
			 * 合计
			 */
			handleTotal() {
				this.moneyTitle = Number(Number(this.peopleTotal) * Number(this.price))
			},
			/**
			 * 选择主要人物性别
			 * @param {Object} i
			 */
			handleIndex(i) {
				this.manWomanIndex = i
				this.formData.Gender = this.manWoman[i].text
			},
			/**
			 * 选择挂件人性别
			 * @param {Object} index
			 */
			handleSex(index) {
				this.sex = index
				this.hangerObject.sex = this.manWoman[index].text
			},

			/**
			 * 添加挂件人信息
			 */
			addPendantMan() {
				this.addNoHanger = true
				this.$refs.popup.open()
				this.hangerObject.name = ''
				this.hangerObject.sex = ''
				this.sex = null
			},
			onSubmit() {
				//添加挂件人
				if (this.addNoHanger === true) {
					this.hangerList.push({
						name: this.hangerObject.name,
						sex: this.hangerObject.sex
					})
					this.Pendant.push(`${this.hangerObject.name}|${this.hangerObject.sex}`)
					this.formData.Pendant = this.Pendant.toString()
					this.peopleTotal = this.peopleTotal + 1
					this.handleTotal()
					this.$refs.popup.close()
					return false
				}
				//编辑挂件人
				this.hangerList[this.editIndex].name = this.hangerObject.name
				this.hangerList[this.editIndex].sex = this.hangerObject.sex
				this.Pendant[this.editIndex] = `${this.hangerObject.name}|${this.hangerObject.sex}`
				this.formData.Pendant = this.Pendant.toString()
				this.$refs.popup.close()

			},
			delectHanger(index) {
				this.hangerList.splice(index, 1)
				this.Pendant.splice(index, 1)
				this.formData.Pendant = this.Pendant.toString()
				this.peopleTotal = this.peopleTotal - 1
				this.handleTotal()
			},
			editHanger(index) {
				this.addNoHanger = false
				this.editIndex = index
				this.hangerObject.name = this.hangerList[index].name
				this.hangerObject.sex = this.hangerList[index].sex
				this.sex = this.hangerObject.sex === '男' ? 0 : 1
				console.log(`${this.hangerObject.name}|${this.hangerObject.sex}`)
				this.$refs.popup.open()
			},
			/**
			 * 选择活动分组
			 * @param {Object} index
			 */
			handleGrouping(index) {
				if (this.signDetailList[index].GourpSign === this.signDetailList[index].SignNumber) {
					uni.showToast({
						title: '人数已满，请选择其他活动分组！',
						icon: 'error'
					})
					return false
				}
				this.signDetail = index
				this.formData.GroupingID = this.signDetailList[index].GroupingID
				this.price = this.signDetailList[index].Price
				this.handleTotal()
			},
			/**
			 * 填写昵称添加人数
			 */
			handleNickName() {
				if (this.formData.NickName) {
					this.peopleTotal = this.peopleTotal + 1
					this.handleTotal()
				}
			},
			/**
			 * 判断用户否已填写信息
			 */
			validateForm() {
				if (!this.validForm(this.formData, [{
							key: "NickName",
							text: "请输入昵称"
						},
						{
							key: "Gender",
							text: "请选择性别"
						},
						{
							key: "Phone",
							text: "请输入手机号"
						},
						{
							key: "GroupingID",
							text: "请选择活动分组"
						}
					])) {
					return false
				}
				if (!(/^1(3|4|5|6|7|8|9)\d{9}$/.test((this.formData.Phone).replace(/\s+/g, "")))) {
					uni.showToast({
						title: "手机号码有误，请重新填写",
						icon: 'error'
					})
					return false;
				}
				return true
			},
			/**
			 * 提交报名
			 */
			async eventRegistration() {
				if (!this.validateForm()) {
					return false
				}
				console.log('shuju', this.formData)
				const result = await PostCreateActivitySignAjax({
					...this.formData,
					token: true
				})
				console.log('fanhui', result.Result)
				if (result.Status !== 1) {
					uni.showToast({
						title: result.Result,
						icon: 'error'
					})
					return false
				}

				const payOrder = await GetOrderPayAjax({
					orderNo: result.Result,
					payType: 2,
					token: true
				})
				let that = this
				uni.requestPayment({
					"provider": 'wxpay',
					"signType": "RSA",
					"appId": payOrder.Result.appId,
					"nonceStr": payOrder.Result.nonceStr,
					"package": payOrder.Result.package,
					"partnerId": payOrder.Result.partnerId,
					"prepayId": payOrder.Result.prepayId,
					"timeStamp": payOrder.Result.timeStamp,
					"paySign": payOrder.Result.paySign,
					success: function(res) {
						uni.showToast({
							title: '支付成功！',
							icon: 'success'
						})
						setTimeout(() => {
							uni.navigateTo({
								url: '/pages/activity/registration-successful/index'
							})
						})
					},
					fail: function(err) {
						uni.showToast({
							title: '支付失败！',
							icon: 'error'
						})
						console.log('fail:' + JSON.stringify(err));
					}
				})
			},
		}
	}
</script>

